import React from 'react';
import './Thread.css';
import {Link} from "react-router-dom";


export const Thread = ({title, id, content, number, date, comments, allComments}) => {

    const noComments = <div className="thread__noComments">
        there are no comments yet
    </div>

    const commentsBlock = <div className="thread__comments">
        {comments.map((elem) => <div className="thread__comment">
                <div className="thread__detail">Anon {new Date(elem.pub_date).toLocaleString()}</div>
                <p className="thread__text">{elem.content}</p>
            </div>
        )}
        {allComments && allComments.length !== comments.length &&
        <Link to={{pathname: `/thread/${id}`, fromDashboard: false}}
              className='thread__allComments'>view all comments</Link>}
    </div>

    return (
        <div className="thread">
            <div className="thread__main">
                <div className="thread__detail">Anon {date} №{number}</div>
                <h3 className="thread__title">{title}</h3>
                <p className="thread__text">{content}</p>
            </div>
            <div className="thread__comments">
                {comments.length === 0 ? noComments : commentsBlock}
            </div>
        </div>
    )
}
