import React, { useEffect, useState } from 'react';
import './ThreadDetailPage.css'
import { Thread } from "../../commons/Thread";
import axios from "axios";


export const ThreadDetailPage = ({ match }) => {
    const [data, setData] = useState({});
    const id = match.params.id;

    useEffect(() => {
        axios({
            method: "GET",
            url: `http://127.0.0.1:8000/api/thread/${id}/`
        }).then((res) => {
            let data = res.data
            data.comments.sort((a, b) => {
                return b.id - a.id
            })
            console.log(data.slug.slice(data.slug.indexOf('-') + 1))
            setData(data);
        })
    }, [])

    return (
        <>
            {/*{data && <Thread content={data.content} title={data.title}*/}
            {/*                               date={new Date(data.pub_date).toLocaleString()}*/}
            {/*                               number={data.slug.slice(data.slug.indexOf('-') + 1)}*/}
            {/*                               comments={data.comments}/>}*/}
        </>
    )

}
